import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";
import { toast } from "sonner";
import { useTranslation } from "react-i18next";

export interface PostRestaurant {
  id: string;
  name: string;
  name_en: string | null;
  name_pt: string | null;
}

export interface CommunityPost {
  id: string;
  author_id: string;
  content: string;
  content_en: string | null;
  content_zh: string | null;
  content_pt: string | null;
  image_urls: string[];
  video_url: string | null;
  weight: number;
  activity_id: string | null;
  restaurant_id: string | null; // Legacy single restaurant
  status: string;
  moderation_status: string;
  risk_score: number | null;
  risk_reasons: string[] | null;
  ai_reviewed_at: string | null;
  likes_count: number;
  comments_count: number;
  shares_count: number;
  views_count: number;
  created_at: string;
  updated_at: string;
  author?: {
    id: string;
    display_name: string;
    avatar_url: string | null;
  };
  activity?: {
    id: string;
    title_en: string;
    title_zh: string;
    title_pt: string;
  };
  restaurant?: {
    id: string;
    name: string;
    name_en: string | null;
    name_pt: string | null;
  };
  // Multiple restaurants via junction table
  restaurants?: PostRestaurant[];
  user_liked?: boolean;
}

export type SortOption = 'latest' | 'popular';

export const useCommunityPosts = (options?: { 
  authorId?: string; 
  status?: string;
  limit?: number;
  sortBy?: SortOption;
}) => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["community-posts", options],
    queryFn: async () => {
      let query = supabase
        .from("community_posts")
        .select(`
          *,
          author:profiles!community_posts_author_id_fkey(id, display_name, avatar_url),
          activity:activities(id, title_en, title_zh, title_pt),
          restaurant:restaurants(id, name, name_en, name_pt),
          post_restaurants(
            restaurant:restaurants(id, name, name_en, name_pt)
          )
        `);

      // Apply sorting based on sortBy option
      if (options?.sortBy === 'popular') {
        // Sort by likes_count first, then by created_at
        query = query
          .order("likes_count", { ascending: false })
          .order("created_at", { ascending: false });
      } else {
        // Default: sort by weight first, then by created_at (latest)
        query = query
          .order("weight", { ascending: false })
          .order("created_at", { ascending: false });
      }

      if (options?.authorId) {
        query = query.eq("author_id", options.authorId);
      }

      if (options?.status) {
        query = query.eq("status", options.status);
      }

      if (options?.limit) {
        query = query.limit(options.limit);
      }

      const { data, error } = await query;

      if (error) throw error;

      // Transform data to include restaurants array
      const transformedData = data?.map((post: any) => ({
        ...post,
        restaurants: post.post_restaurants?.map((pr: any) => pr.restaurant).filter(Boolean) || [],
      }));

      // Check if user has liked each post
      if (user && transformedData) {
        const postIds = transformedData.map((post) => post.id);
        const { data: likes } = await supabase
          .from("post_likes")
          .select("post_id")
          .eq("user_id", user.id)
          .in("post_id", postIds);

        const likedPostIds = new Set(likes?.map((l) => l.post_id) || []);

        return transformedData.map((post) => ({
          ...post,
          user_liked: likedPostIds.has(post.id),
        })) as CommunityPost[];
      }

      return transformedData as CommunityPost[];
    },
  });
};

export const useCommunityPost = (postId: string) => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["community-post", postId],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("community_posts")
        .select(`
          *,
          author:profiles!community_posts_author_id_fkey(id, display_name, avatar_url),
          activity:activities(id, title_en, title_zh, title_pt),
          restaurant:restaurants(id, name, name_en, name_pt),
          post_restaurants(
            restaurant:restaurants(id, name, name_en, name_pt)
          )
        `)
        .eq("id", postId)
        .single();

      if (error) throw error;

      // Transform to include restaurants array
      const transformedData = {
        ...data,
        restaurants: (data as any).post_restaurants?.map((pr: any) => pr.restaurant).filter(Boolean) || [],
      };

      // Check if user has liked
      if (user) {
        const { data: like } = await supabase
          .from("post_likes")
          .select("id")
          .eq("post_id", postId)
          .eq("user_id", user.id)
          .maybeSingle();

        return { ...transformedData, user_liked: !!like } as CommunityPost;
      }

      return transformedData as CommunityPost;
    },
    enabled: !!postId,
  });
};

export const useCreatePost = () => {
  const queryClient = useQueryClient();
  const { t } = useTranslation();

  return useMutation({
    mutationFn: async (post: {
      content: string;
      image_urls?: string[];
      video_url?: string;
      activity_id?: string;
      restaurant_ids?: string[];
      sensitive_word_detected?: boolean;
    }) => {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error("Not authenticated");

      // Insert post with pending status
      const { data, error } = await supabase
        .from("community_posts")
        .insert({
          author_id: user.id,
          content: post.content,
          image_urls: post.image_urls || [],
          video_url: post.video_url || null,
          activity_id: post.activity_id || null,
          restaurant_id: null, // Legacy field, use post_restaurants instead
          status: "pending",
          moderation_status: "pending",
        })
        .select()
        .single();

      if (error) throw error;

      // Insert post_restaurants relations
      if (post.restaurant_ids && post.restaurant_ids.length > 0) {
        const { error: relError } = await supabase
          .from("post_restaurants")
          .insert(
            post.restaurant_ids.map((restaurantId) => ({
              post_id: data.id,
              restaurant_id: restaurantId,
            }))
          );
        if (relError) {
          console.error("Failed to insert post restaurants:", relError);
        }
      }

      // Call moderation function and wait for it to complete
      try {
        await supabase.functions.invoke("moderate-post", {
          body: {
            post_id: data.id,
            content: post.content,
            user_id: user.id,
            image_urls: post.image_urls || [],
            video_url: post.video_url || null,
            sensitive_word_detected: post.sensitive_word_detected || false,
          },
        });
      } catch (err) {
        console.error("Moderation call failed:", err);
      }

      return data;
    },
    onSuccess: () => {
      // Invalidate queries to refresh the list
      queryClient.invalidateQueries({ queryKey: ["community-posts"] });
      queryClient.invalidateQueries({ queryKey: ["has-posted-today"] });
      toast.success(t("community.postSuccess", "帖子发布成功"));
    },
    onError: (error) => {
      console.error("Create post error:", error);
      toast.error(t("community.postError", "发布失败"));
    },
  });
};

export const useUpdatePost = () => {
  const queryClient = useQueryClient();
  const { t } = useTranslation();

  return useMutation({
    mutationFn: async ({
      id,
      ...updates
    }: {
      id: string;
      content?: string;
      image_urls?: string[];
      video_url?: string | null;
      activity_id?: string | null;
      weight?: number;
      status?: string;
      moderation_status?: string;
    }) => {
      const { data, error } = await supabase
        .from("community_posts")
        .update(updates)
        .eq("id", id)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["community-posts"] });
      queryClient.invalidateQueries({ queryKey: ["community-post"] });
      queryClient.invalidateQueries({ queryKey: ["admin-community-posts"] });
      toast.success(t("common.saved", "保存成功"));
    },
    onError: (error) => {
      console.error("Update post error:", error);
      toast.error(t("common.error", "操作失败"));
    },
  });
};

export const useDeletePost = () => {
  const queryClient = useQueryClient();
  const { t } = useTranslation();

  return useMutation({
    mutationFn: async (id: string) => {
      const { error } = await supabase
        .from("community_posts")
        .delete()
        .eq("id", id);

      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["community-posts"] });
      queryClient.invalidateQueries({ queryKey: ["admin-community-posts"] });
      toast.success(t("common.deleted", "删除成功"));
    },
    onError: (error) => {
      console.error("Delete post error:", error);
      toast.error(t("common.error", "操作失败"));
    },
  });
};

export const useLikePost = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ postId, liked }: { postId: string; liked: boolean }) => {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error("Not authenticated");

      if (liked) {
        // Unlike
        const { error } = await supabase
          .from("post_likes")
          .delete()
          .eq("post_id", postId)
          .eq("user_id", user.id);

        if (error) throw error;
      } else {
        // Like
        const { error } = await supabase
          .from("post_likes")
          .insert({
            post_id: postId,
            user_id: user.id,
          });

        if (error) throw error;
      }
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["community-posts"] });
      queryClient.invalidateQueries({ queryKey: ["community-post"] });
    },
  });
};

export const useSharePost = () => {
  const queryClient = useQueryClient();
  const { t } = useTranslation();

  return useMutation({
    mutationFn: async (postId: string) => {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error("Not authenticated");

      const { error } = await supabase
        .from("post_shares")
        .insert({
          post_id: postId,
          user_id: user.id,
        });

      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["community-posts"] });
      queryClient.invalidateQueries({ queryKey: ["community-post"] });
      toast.success(t("community.shared", "分享成功"));
    },
  });
};

export const useCanCreatePost = () => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["can-create-post", user?.id],
    queryFn: async () => {
      // 现在所有登录用户都可以发帖
      return !!user;
    },
    enabled: true,
  });
};

// 获取商家关联的餐厅
export const useMerchantRestaurant = () => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["merchant-restaurant", user?.id],
    queryFn: async () => {
      if (!user) return null;

      const { data } = await supabase
        .from("merchant_restaurants")
        .select("restaurant_id, restaurants:restaurant_id(id, name, name_en, name_pt)")
        .eq("user_id", user.id)
        .limit(1)
        .maybeSingle();

      return data?.restaurants || null;
    },
    enabled: !!user,
  });
};

// Check if user has posted today
export const useHasPostedToday = () => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["has-posted-today", user?.id],
    queryFn: async () => {
      if (!user) return false;

      // Get start of today (local time)
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      const todayISO = today.toISOString();

      // Check if user has posted today (any status)
      const { count, error } = await supabase
        .from("community_posts")
        .select("id", { count: "exact", head: true })
        .eq("author_id", user.id)
        .gte("created_at", todayISO);

      if (error) throw error;
      return (count ?? 0) > 0;
    },
    enabled: !!user,
  });
};

// Admin hooks
export const useAdminCommunityPosts = (options?: {
  status?: string;
  moderation_status?: string;
  search?: string;
}) => {
  return useQuery({
    queryKey: ["admin-community-posts", options],
    queryFn: async () => {
      let query = supabase
        .from("community_posts")
        .select(`
          *,
          author:profiles!community_posts_author_id_fkey(id, display_name, avatar_url),
          activity:activities(id, title_en, title_zh, title_pt),
          restaurant:restaurants(id, name, name_en, name_pt)
        `)
        .order("created_at", { ascending: false });

      if (options?.status) {
        query = query.eq("status", options.status);
      }

      if (options?.moderation_status) {
        query = query.eq("moderation_status", options.moderation_status);
      }

      if (options?.search) {
        query = query.ilike("content", `%${options.search}%`);
      }

      const { data, error } = await query;

      if (error) throw error;
      return data as CommunityPost[];
    },
  });
};
